.card-front {
    background-image: url(../../images/bg-card-front.png) ;
    @include card;
    color: $white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5%;
    position: absolute;
    top: 54%;
    left: 1em;
    box-shadow: 3px 4px 17px -1px rgba(51,51,51,0.69);

    & .decorative {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        & .circle {
            border-radius: 100%;
            aspect-ratio: 1/1;
        }
    
        & ._big {
            width: 15%;
            background-color: $white;
            margin-right: 5%;
        }
    
        & ._small {
            width: 7.5%;
            border: 1px solid $white;
        } 
    }

    & .card-number {
        margin-top: .8em;

        & .__digits {
            font-size: 1.3rem;
            letter-spacing: 0.05em;
        }
    }

    & .card_user {
        display: flex;
        justify-content: space-between;
        font-size: 0.8rem;
        letter-spacing: 0.1em;

        & .__user-name {
            text-transform: uppercase;
        }
    }
}

@media all and (min-width: 767px) {
    .card-front {
        position: unset;

        & .card-number {
            & .__digits {
                font-size: 1.35rem;
            }
        }
    }
}

@media all and (min-width: 1024px) {
    .card-front {
        margin-bottom: 2.5em;
        transform: translateX(40%);
        padding: 6%;

        & .card-number {
            & .__digits {
                font-size: 1.6rem;
            }
        }

        & .card_user {
            font-size: 1rem;
        }
    }
}

    
